.modal_title {
  background: #61dafb;
  border-radius: 5px;
  padding-left: 5px;
  line-height: 30px;
  font-size: 13pt;
  height: 30px;
}
.question_list {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  padding-left: 5px;
  margin-top: 10px;
  height: 30px;
  border-bottom: 2px solid #7f7f7f;

  .question_title,
  .question_answer {
    font-size: 1rem;
    line-height: 20px;
    em {
      font-size: 0.5rem;
    }
    .button_area {
      display: flex;
      flex-flow: row nowrap;
      margin: 0 !important;
      input {
        margin-left: 10px;
      }
      p {
        margin: 0 10px;
      }
    }
  }
  .select_btn_area {
    display: flex;
    flex-flow: row nowrap;
    color: #000000;
    margin: 0 5px 0 20px;
    font-weight: bold;
    font-size: 2rem;
    text-align: center;
    button {
      background: #53f1fb;
      margin-bottom: 10px;
      border-radius: 10px;
      border: 0;
    }
  }
}


