body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

p, em, strong {
  margin: 0;
  padding: 0;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.main-container {
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin); }

.form-info__title {
  display: block;
  margin: 30px 20px 30px;
  width: 85vmin; }
  .form-info__title div {
    float: left;
    left: 0; }
    .form-info__title div p {
      font-size: 0.8rem; }
  .form-info__title button {
    float: right;
    right: 0; }

/* question area box */
.question_box {
  background: #bcbdbe;
  width: 90vmin;
  margin-bottom: 10px;
  border-radius: 10px;
  text-align: center; }
  .question_box .question_header {
    background: #61dafb;
    border-radius: 10px;
    text-align: left; }
    .question_box .question_header p {
      margin: 0;
      padding: 0 10px; }
  .question_box .button_area {
    display: flex;
    flex-flow: row nowrap;
    justify-content: center; }
    .question_box .button_area button {
      margin: 10px 20px; }
  .question_box button {
    margin: 10px 0;
    width: 100px;
    height: 30px;
    border-radius: 10px;
    border: 0; }

/* q1 */
#question_1 #q1_header em {
  font-size: 0.7rem; }

#question_1 .button_area {
  display: flex;
  flex-flow: column nowrap; }
  #question_1 .button_area span {
    display: flex;
    flex-flow: row nowrap;
    margin: 5px 0; }
    #question_1 .button_area span p {
      margin-left: 10px; }

/* date picker */
.form-info {
  text-align: center;
  font-size: 9pt;
  width: 85vmin;
  margin: 10px 20px 20px; }
  .form-info #address {
    margin-top: 10px;
    width: 85vmin;
    border-width: 3px; }
  .form-info div {
    margin-top: 20px; }
    .form-info div input[type="radio"] {
      margin-right: 10px;
      float: left; }
    .form-info div input[type="text"] {
      margin-left: 20px;
      min-width: 200px;
      float: right;
      right: 0;
      border-width: 3px; }
  .form-info button {
    margin-top: 20px;
    width: 30vmin;
    height: 30px;
    border-width: 3px;
    background: #61dafb; }

.modal_title {
  background: #61dafb;
  border-radius: 5px;
  padding-left: 5px;
  line-height: 30px;
  font-size: 13pt;
  height: 30px; }

.question_list {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  padding-left: 5px;
  margin-top: 10px;
  height: 30px;
  border-bottom: 2px solid #7f7f7f; }
  .question_list .question_title,
  .question_list .question_answer {
    font-size: 1rem;
    line-height: 20px; }
    .question_list .question_title em,
    .question_list .question_answer em {
      font-size: 0.5rem; }
    .question_list .question_title .button_area,
    .question_list .question_answer .button_area {
      display: flex;
      flex-flow: row nowrap;
      margin: 0 !important; }
      .question_list .question_title .button_area input,
      .question_list .question_answer .button_area input {
        margin-left: 10px; }
      .question_list .question_title .button_area p,
      .question_list .question_answer .button_area p {
        margin: 0 10px; }
  .question_list .select_btn_area {
    display: flex;
    flex-flow: row nowrap;
    color: #000000;
    margin: 0 5px 0 20px;
    font-weight: bold;
    font-size: 2rem;
    text-align: center; }
    .question_list .select_btn_area button {
      background: #53f1fb;
      margin-bottom: 10px;
      border-radius: 10px;
      border: 0; }

#modal_outer {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  z-index: 1; }
  #modal_outer #modal_overlay {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5); }
  #modal_outer #modal {
    display: flex;
    flex-flow: column nowrap;
    position: relative;
    width: 90vmin;
    padding: 20px;
    box-sizing: border-box;
    background-color: #ffffff;
    margin: 40px auto;
    border-radius: 3px;
    z-index: 2;
    text-align: left;
    box-shadow: 0 20px 30px rgba(0, 0, 0, 0.2); }
    #modal_outer #modal .button_area {
      margin-top: 20px; }
      #modal_outer #modal .button_area #plus_btn {
        float: left;
        width: 120px;
        margin-left: 0; }
      #modal_outer #modal .button_area button {
        background: #53f1fb;
        margin: 0 0 0 10px;
        float: right;
        height: 30px;
        width: 60px;
        border: 0;
        border-radius: 10px;
        font-weight: bold; }

#loan_type {
  display: flex;
  flex-flow: column nowrap;
  height: auto; }
  #loan_type .question_answer {
    margin-bottom: 10px; }
    #loan_type .question_answer .button_area {
      display: flex;
      flex-flow: column; }
      #loan_type .question_answer .button_area span {
        display: flex;
        flex-flow: row nowrap;
        margin-top: 10px; }
        #loan_type .question_answer .button_area span input {
          margin: 0; }

