.main-container {
  //background-color: #282c34;
  //min-height: 100vh;
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  //color: white;
}

.form-info__title {
  display: block;
  margin: 30px 20px 30px;
  width: 85vmin;

  div {
    float: left;
    left: 0;
    p {
      font-size: 0.8rem;
    }
  }
  button {
    float: right;
    right: 0;
  }
}

/* question area box */
.question_box {
  background: #bcbdbe;
  width: 90vmin;
  margin-bottom: 10px;
  border-radius: 10px;
  text-align: center;

  .question_header {
    background: #61dafb;
    border-radius: 10px;
    text-align: left;
    p {
      margin: 0;
      padding: 0 10px;
    }
  }
  .button_area {
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    button {
      margin: 10px 20px;
    }
  }
  button {
    margin: 10px 0;
    width: 100px;
    height: 30px;
    border-radius: 10px;
    border: 0;
  }
}

/* q1 */
#question_1 {
  #q1_header {
    em {
      font-size: 0.7rem;
    }
  }
  .button_area {
    display: flex;
    flex-flow: column nowrap;
    span {
      display: flex;
      flex-flow: row nowrap;
      margin: 5px 0;
      p {
        margin-left: 10px;
      }
    }
  }
}

/* date picker */
.form-info {
  text-align: center;
  font-size: 9pt;
  width: 85vmin;
  margin: 10px 20px 20px;

  #address {
    margin-top: 10px;
    width: 85vmin;
    border-width: 3px;
  }

  div {
    //display: flex;
    //flex-flow: row nowrap;
    margin-top: 20px;

    input[type="radio"] {
      margin-right: 10px;
      float: left;
    }

    input[type="text"] {
      margin-left: 20px;
      //width: 50vmin;
      min-width: 200px;
      float: right;
      right: 0;
      border-width: 3px;

    }
  }

  button {
    margin-top: 20px;
    width: 30vmin;
    height: 30px;
    border-width: 3px;
    background: #61dafb;
  }
}



