#modal_outer {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  z-index: 1;

  #modal_overlay {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.5);
    //...this.props.style.overlay
  }
  #modal {
    display: flex;
    flex-flow: column nowrap;
    position: relative;
    width: 90vmin;
    padding: 20px;
    box-sizing: border-box;
    background-color: #ffffff;
    margin: 40px auto;
    border-radius: 3px;
    z-index: 2;
    text-align: left;
    box-shadow: 0 20px 30px rgba(0, 0, 0, 0.2);
    //...this.props.style.modal
    .button_area {
      margin-top: 20px;
      #plus_btn {
        float: left;
        width: 120px;
        margin-left: 0;
      }
      button {
        background: #53f1fb;
        margin: 0 0 0 10px;
        float: right;
        height: 30px;
        width: 60px;
        border: 0;
        border-radius: 10px;
        font-weight: bold;
      }
    }
  }
}